import { useLaunch, useError } from "@tarojs/taro";
import { default as Monitor } from "@monitor/sdk/taro";
import "./app.scss";
function App({
  children
}) {
  useLaunch(() => {
    console.log("App launched.");

    // SDK
    try {
      // IPlocalhost
      // IPlocalhost
      const serverUrl = "http://localhost:3001"; // localhost:3001

      console.log(" Monitor SDK:", serverUrl);

      // 1: 
      const config = {
        projectId: "taromini",
        projectVersion: "1.0.0",
        // 
        serverUrl,
        enableInDev: true,
        // 
        debug: true,
        // 

        //  - 
        error: {
          enabled: true,
          captureConsole: true,
          // console.error
          maxErrors: 50,
          sampleRate: 1 // 100%
        },
        //  - 
        performance: {
          enabled: true,
          capturePageLoad: true,
          // 
          captureNetworkTiming: true,
          // 
          maxPerformance: 100
        },
        //  - 
        behavior: {
          enabled: true,
          capturePageViews: true,
          // 
          captureTaps: true,
          // 
          captureRouteChange: true,
          // 
          maxBehaviors: 200
        },
        // 
        report: {
          interval: 15000,
          // 15
          batchSize: 10,
          maxRetries: 2
        }
      };

      // init
      const sdkInstance = Monitor.init(config);
      console.log(" Monitor SDK :", sdkInstance);

      // SDK
      setTimeout(() => {
        const sdkStatus = Monitor.getStatus?.();
        console.log(" SDK:", sdkStatus);
        if (!sdkStatus?.initialized) {
          console.error(" SDK:");
          console.error("1. ");
          console.error("2. ");
          console.error("3. Taro");
        } else {
          console.log(" SDK!");
        }
      }, 1000);
    } catch (error) {
      console.error(" Monitor SDK :", error);
      console.error(":", error.message);
      console.error(":", error.stack);
    }
    console.log("Monitor SDK initialized with new version");
  });

  // useError Hook
  useError(error => {
    console.log("useError Hook:", error);

    // SDK
    // 
    // Monitor.captureError(error, { context: 'useError_hook' });
  });

  // AppuseError Hook
  return children;
}
export default App;